@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,700,800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Montserrat",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* Main stores all the color and general theme */
.alert-success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
    box-shadow: 0px 3px 40px rgba(0,0,0,.17);
}

.alert {
    position: relative;
    display: block;
    padding: .75rem 1.25rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: .25rem;
    width: 340px;
    margin: 0 auto;
    text-align: center;
}

* {
    --mainBlue: #3B55E6;
    --subBlue: #7E85AA;
    --mainOrange: #E67E22;
    --mainGreen: #27AE60;
    --background: #3498DB;
    --container: #fff;
    --border: #F0F2F7;
    --inputbg: #F0F2F7;
    --inputborder: #E3E8F0;
    --avatar: #EAEAEA;
    font-family: 'Montserrat';

}
.register, .selecttime {
    margin-top: 4em;
}

.selecttime, .direction{
    position: relative;
    overflow: hidden;
    height: 100%;
}

body {
    background: var(--background);
    height: 100vh;
    position: relative;
}
h1.logo-title{
    font-weight: 700;
    color: white;
    display: block;
    text-align: center;
}
  
.logo-title svg {
margin-right: .2em;
}

#root{
    height: 100%;
}
button:hover{
    background: #fff radial-gradient(circle, transparent 1%, #fff 1%) center/15000%;
}
button:active{
    background-color: #E3E8F0;
    background-size: 100%;
    transition: background 0s;
}
button {
    background: white;
    background-position: center;
    transition: background 0.8s;
}
button.bigBtn {
    width: 300px;
    height: 45px;
    border-radius: 99px;
    color: black;
    border: 0;
    box-shadow: 0px 3px 6px rgba(0,0,0,.17);
    font-weight: 700;
}



.bigBtn:hover{
    cursor: pointer;
}


button:focus {
    outline: 0;
}

.container{
    padding: 1em 1em;
    background: var(--container);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    min-height: 100%;
    max-width: 350px;
    margin: 1em auto;
    border-radius: 5px;
    box-shadow: 0px 3px 40px rgba(0,0,0,.17);
}

.container label{ 
    color:var(--mainBlue);
    text-transform: uppercase;
    font-weight: 800;
    font-size: .8em;
    display: block;
    margin-bottom: 1em;
}
.container .form .element-inline {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2em;
    margin-bottom: 1em;
}
.container .form .element{
    margin-bottom: 1em;
}
.container .header{
    border-bottom: 1px solid var(--border);
}

.container .form {
    padding: 1em 2em;
}

.container .form input{
    background: var(--inputbg);
    border: 1px solid var(--inputborder);
    border-radius: 99px;
    line-height: 3em;
    box-shadow: inset 0;
    margin-left: -15px;
    padding-left: 1em;
    display: block;
    width: calc(100%);
    color: var(--mainBlue);

}

.container .form select{
    background: var(--inputbg);
    border: 1px solid var(--inputborder);
    border-radius: 99px;
    line-height: 3em;
    height: 3em;
    box-shadow: inset 0;
    margin-left: -15px;
    padding-left: 1em;
    display: block;
    width: calc(106%);
}
.container .form select:focus{
    outline: 0;
}


.container .form input:focus{
    outline: 0;
}
.container .form .element.divider{ 
    border: 2px solid var(--border);
    border-left:0;
    border-right:0;
    border-bottom: 0;
    padding-top: 1em;
    width: 100%;
}
#round {
    border-radius: 25px;
    background:#3B55E6;
     
}
hr {
    border-color: var(--border)
}
button.bigBtn.blueTheme{
    background: var(--mainBlue);
    letter-spacing: inherit;
    color: white;
    text-transform: uppercase;
}
button.bigBtn.blueTheme:hover{
    background: var(--mainBlue) radial-gradient(circle, transparent 1%, var(--mainBlue) 1%) center/15000%;
}
button.bigBtn.blueTheme:active{
    background-color: #E3E8F0;
    background-size: 100%;
    transition: background 0s;
}

[type="checkbox"]
{
    vertical-align:middle;
}

.module{
    padding: 0 3em;
}
.instruction {
    color: white;
    font-weight: 600;
    text-align: center;
    margin-bottom:2em;
}

.sml-container{
    background: white;
    border-radius: 32px;
    padding: 1em;
    z-index: 999;
    position: relative;
}

.sml-container h3{
    color: black;
    letter-spacing: .15em;
    text-transform: uppercase;
    font-size: .8em;
    text-align: center;
    margin: 3em auto 2em;
}

.sml-container h3.blueTheme{
    color: var(--mainBlue);
    letter-spacing: .15em;
    text-transform: uppercase;
    font-size: .8em;
    text-align: center;
}
.hourSelection{
    margin: 0 auto;
    display: block;
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    margin: 2em auto 2em;
}
input[type="number"]{
    border: 0;
    color: #000;
    font-size: 2em;
    font-weight: 600;
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 50px;
}

input[type="number"]:focus{
    outline: 0;
}

input[type="number"]::-webkit-input-placeholder {
    color: black;
}

.hourSelection .seperator{
    display: inline-block;
    line-height: 13px;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

select.genderpref{
    background: var(--inputbg);
    border: 1px solid var(--inputborder);
    border-radius: 99px;
    line-height: 4em;
    height: 4em;
    box-shadow: inset 0;
    margin-left: -15px;
    padding-left: 1em;
    display: block;
    width: 100%;
    text-align: center;
    margin: 0 auto;
}

select:focus{
    outline: none;
}

.sml-container .centerbtn {
    background: var(--mainBlue);
    border-radius: 100%;
    width:94px;
    height: 94px;
    display: block;
    margin: 0 auto;
    text-align: center;
    position: relative;
    box-shadow: 0px 3px 40px rgba(0,0,0,.17);
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
    top: 50%;
    border: 0;
    cursor: pointer;
}
.sml-container .centerbtn svg{
    font-size: 2.2em;
    color: white;
    margin: 0 auto;
    /* /* top: 50%; */
    -webkit-transform: translateX(10%);
            transform: translateX(10%); 
    position: relative;
}

.user{
    background: var(--avatar);
    border-radius: 100%;
    width: 60px;
    height: 60px;
    display: inline-block;
    position: relative;
    margin: 0 auto;
}

.userList{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;

    margin: 0 auto;
    -webkit-align-items: center;
            align-items: center;
}

button.centerbtn.success{
    background: var(--mainGreen);
}

button.centerbtn.success svg{
    -webkit-transform: translateX(0%);
            transform: translateX(0%); 
    font-size: 3em;
}

.sml-container-rnd {
    position: absolute;
    background: white;
    border-radius: 99px;
    padding: 1em;
    z-index: 999;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    box-shadow: 0px 3px 40px rgba(0,0,0,.17);
    bottom: 5em;
    width: 300px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.sml-container-rnd h4{
    width: 200px;
    margin: 0 auto;
}

.sml-container-rnd img{
    width: 100px;
    height: 15px;
    -webkit-transform: scale(4);
            transform: scale(4);
    margin-top: 5px;
}

.user .user-checkbox{
    background: var(--mainBlue);
    width: 15px;
    height: 15px;
    border-radius: 5px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 90%;
    position: absolute;
    box-shadow: 0px 3px 6px rgba(0,0,0,.17);
    border: 0;
    padding: 0;
}

.user .user-checkbox svg{
    color: white;
    font-size: .9em;
}

.status-container{
    display: inline-block;
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-right: 1em;
}

.status-container .status{
    font-size: 12px;
    margin-top: 1em;
    font-weight: 600;
}

.arrived {
    color: var(--mainGreen);
}

.otw{
    color: var(--mainOrange);
}









.qr-container{
    margin-bottom: 2em;
    text-align: center;
    
}



.sml-container-sqr{
    background: white;
    border-radius: 11px;
    padding: 1em;
    position: relative; 
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    margin-bottom: 2em;
    font-weight: 600;
    width: 273px;
    margin: 0 auto;
}

.qr-container img{
    width: 300px;
    height: 300px;
    margin: 0 auto;
    display: block;
    background:white;
    border-radius: 20px;
    box-shadow: 0 3px 6px rgba(0, 3, 50,.16);
}

.status-container.qr-arrive{
    margin-right: 0;
    text-align: center;
    width: 80px;
}
.sml-container-sqr{
    text-align: center;
    margin-bottom: 20px;
}
.outter{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    text-align: center;
    font-size: .9em;
    font-weight: 600;
}

.outter .securetext{
    margin-bottom: 1em;
}

.circleBg{
    position: absolute;
    width: 150vw;
    height: 150vw;
    border-radius: 100%;
    background: #fff;
    bottom: -10%;
    z-index: -1;
    margin: 0 auto;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.instruction h3{
    width: 200px;
    margin: 0 auto;
}

.centerSOS{
    background: white;
    border-radius: 100%;
    width:94px;
    height: 94px;
    display: block;
    margin: 0 auto;
    box-shadow: 0px 3px 40px rgba(0,0,0,.17);
    border: 0;
    cursor: pointer;
    font-size: 3em;
    color: #E74C3C;
}

.bottomWrapper{
    height: 300px;
}

.bottomWrapper h4{
    text-align: center;
    margin-top: 1em;
    text-transform: uppercase;
    color: white;
}

.login {
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-height: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}



.sml-container-rnd.top {
    top: 5em;
    position: absolute;
}

.round-btn {
    border-radius: 99px;
    background: white;
    color: inherit;
    padding: .2em 1em;
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: 0;
    height: 39px;
    font-weight: 700;
    font-size: 1em;
    display: block;
    text-transform: uppercase;
    box-shadow: 0px 3px 6px rgba(0,0,0,.17);
    margin-bottom: 1em;
}
.bottom-container{
    display: block;
    margin: 2em auto;
    text-align: center;
    height: 100%;
}

.round-btn.medium {
    height: 50px;
    padding:.2em 2em;
    position: absolute;
    top: -1.5em;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background: var(--subBlue);
    color: white;
}

.round-btn.medium:hover{
    background: var(--mainBlue);

}

button.round-btn:first-child svg{
    color: #E74C3C;
}

button.round-btn:hover{
    cursor: pointer;
}

button.round-btn:nth-child(2) {
    background: #E74C3C;
    color: #fff;
}

.btn-controller {
    position: absolute;
    z-index: 2;
    top: 180px;
    margin-left: 20px;
}

.bottomUI {
    width: 100%;
    border-radius: 30px 30px 0 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 150px;
    background: white;
    position: absolute;
    bottom:0px;
    z-index: 2;
    box-shadow: 0px -3px 46px rgba(0,0,0,.17);
}


.Loading img{
    width: 100px;
    height: 100px;
    position: relative;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
}

.Loading {
    position: absolute;
    z-index: 2;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
}
.camera-square {
    width: 300px;
    height: 200px;
    border: 5px dotted lightblue;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 99;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
